import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth } from "../../../actions";
import {
  FC_GetSchoolByDistrict_2,
  FC_RespondTransfer,
  FT_RespondTransfer,
} from "../../../actions/transfer.action";
import Alert from "../../../components/Alert/Alert";
import Button from "../../../components/Buttons/Button";
import DownloadLink from "../../../components/DownloadLink/DownloadLink";
import InputSelect from "../../../components/Inputs/InputSelect";
import TextArea from "../../../components/Inputs/TextArea";
import Loading from "../../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import {
  SchoolsType_2,
  TransferRequestType,
} from "../../../types/TransferTypes";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../utils/functions";

type TransferFormProps = {
  auth: Auth;
  selectedUserInfo: TransferRequestType;
  closeModalHandler: (element: string) => void;

  //   REDUX ============
  FC_RespondTransfer: FT_RespondTransfer;
};

type TransferFormState = {
  loading: boolean;
  requestLoading: boolean;
  selectedSchool: string;
  comment: string;
  schools: SchoolsType_2[];
  error: {
    target: "main-error" | "main-success" | "comment" | "school";
    msg: string;
  } | null;
};

export class TransferForm extends Component<
  TransferFormProps,
  TransferFormState
> {
  constructor(props: TransferFormProps) {
    super(props);

    this.state = {
      loading: true,
      requestLoading: false,
      selectedSchool: this.props.selectedUserInfo.requested_school_id,
      comment: "",
      schools: [],
      error: null,
    };
  }

  componentDidMount = () => {
    if (
      this.props.auth.district &&
      this.props.auth.district.district_code &&
      this.props.selectedUserInfo.position_code &&
      this.props.selectedUserInfo.qualification_id
    ) {
      FC_GetSchoolByDistrict_2(
        this.props.auth.district.district_code,
        this.props.selectedUserInfo.teacher_id,
        (status: boolean, data: SchoolsType_2[] | null, msg: string) => {
          this.setState({ loading: status });
          if (!status && !isEmptyOrSpaces(msg)) {
            this.setState({
              error: {
                target: "main-error",
                msg: msg,
              },
            });
          } else if (!status && data !== null) {
            const newData = data.filter(
              (item) =>
                item.vacant_posts > 0 ||
                item.school_code.toString() ===
                  this.props.selectedUserInfo.requested_school_id
            );
            this.setState({ schools: newData });
          }
        }
      );
    } else {
      if (
        this.props.auth.district === null ||
        this.props.auth.district.district_code === "" ||
        this.props.auth.district.district_code === null
      ) {
        this.setState({
          error: {
            target: "main-error",
            msg: "No district attached to you!",
          },
        });
      } else if (
        this.props.selectedUserInfo.position_code === null ||
        isEmptyOrSpaces(this.props.selectedUserInfo.position_code)
      ) {
        this.setState({
          error: {
            target: "main-error",
            msg: "No position code found!",
          },
        });
      } else if (
        this.props.selectedUserInfo.qualification_id === null ||
        isEmptyOrSpaces(this.props.selectedUserInfo.qualification_id)
      ) {
        this.setState({
          error: {
            target: "main-error",
            msg: "No qualification id found!",
          },
        });
      }
    }
  };

  RequestHandle = (decision: "REJECTED" | "APPROVED") => {
    if (isEmptyOrSpaces(this.state.selectedSchool))
      return this.setState({ error: { target: "school", msg: "Required!" } });
    const thisSchool = this.state.schools.find(
      (item) => item.school_code === this.state.selectedSchool
    );
    if (thisSchool === undefined)
      return this.setState({
        error: { target: "school", msg: "Can't find the selected school!" },
      });
    if (isEmptyOrSpaces(this.state.comment))
      return this.setState({ error: { target: "comment", msg: "Required!" } });

    this.props.FC_RespondTransfer(
      this.props.selectedUserInfo.teacher_transfer_id.toString(),
      {
        school_name: thisSchool.school_name,
        school_code: thisSchool.school_code,
        sector_name: thisSchool.sector_name,
        district_name: thisSchool.school_code.substring(0, 2),
      },
      decision,
      this.state.comment,
      (status: boolean, response_error: string) => {
        this.setState({ requestLoading: status });
        if (!status && !isEmptyOrSpaces(response_error))
          this.setState({
            error: { target: "main-error", msg: response_error },
          });
        else if (isEmptyOrSpaces(response_error)) {
          this.setState({
            error: { target: "main-success", msg: "Submitted Successfully!" },
            comment: "",
            selectedSchool: "",
          });
          setTimeout(() => {
            this.props.closeModalHandler("");
          }, 1000);
        }
      }
    );
  };

  render() {
    console.log("====================================");
    console.log({
      district: this.props.auth.district,
      position_code: this.props.selectedUserInfo,
      qualification_id: this.props.selectedUserInfo.qualification_id,
    });
    console.log("====================================");
    return (
      <Modal
        backDrop={true}
        theme={Themes.default}
        close={() => {
          this.setState({ selectedSchool: "", comment: "", schools: [] });
          this.props.closeModalHandler("");
        }}
        backDropClose={true}
        widthSizeClass={ModalSize.extraLarge}
        displayClose={true}
        title={
          <div className="text-2xl w-full text-primary-800 -mb-2">
            Teacher(Staff) Information
          </div>
        }
        padding={{
          title: true,
        }}
      >
        <>
          {this.state.error && this.state.error.target === "main-success" && (
            <Alert theme="success" fixed>
              {this.state.error && this.state.error.msg}
            </Alert>
          )}
          {this.state.error && this.state.error.target === "main-error" && (
            <Alert theme="danger" fixed>
              {this.state.error && this.state.error.msg}
            </Alert>
          )}

          {/*body*/}
          <div className="px-4 w-full">
            {/* Information */}
            <div className="p-2">
              <div className="w-full flex flex-col justify-between">
                <div>
                  <h2 className="font-bold text-xl">
                    {!isEmptyOrSpaces(
                      this.props.selectedUserInfo.teacher_full_name
                    )
                      ? this.props.selectedUserInfo.teacher_full_name
                      : this.props.selectedUserInfo.teacher_first_name}{" "}
                    {this.props.selectedUserInfo.teacher_last_name}
                  </h2>

                  {false && (
                    <div className="flex flex-wrap font-bold text-center pt-1 mb-2">
                      <div className="mr-1 mb-2 rounded-full px-3 py-1 text-xs bg-green-200 text-green-900">
                        Teacher
                      </div>

                      <div className="mr-1 mb-2 rounded-full px-3 py-1 text-xs bg-green-200 text-green-900">
                        A0
                      </div>

                      <div className="mr-1 mb-2 rounded-full px-3 py-1 text-xs bg-green-200 text-green-900">
                        math, physics
                      </div>
                    </div>
                  )}

                  <p className="text-md leading-relaxed">
                    <span className="font-bold">Reason:</span>
                    <br />
                    {this.props.selectedUserInfo.teacher_reason}
                  </p>

                  <p className="text-md leading-relaxed">
                    <span className="font-bold">Comment:</span>
                    <br />
                    {this.props.selectedUserInfo.teacher_comment}
                  </p>

                  <ul className="text-xs leading-relaxed">
                    <li>
                      <span className="font-medium">Phone: </span>
                      <span className="font-bold text-base">
                        {this.props.selectedUserInfo.teacher_phone_number}
                      </span>
                    </li>
                    <li>
                      <span className="font-bold">Position: </span>
                      {this.props.selectedUserInfo.position_name}
                    </li>
                    <li>
                      <span className="font-bold">Current School: </span>
                      {this.props.selectedUserInfo.school_from_info.school_name}
                    </li>
                    <li>
                      <span className="font-bold">Requested School: </span>
                      {
                        this.props.selectedUserInfo.requested_school_info
                          .school_name
                      }
                    </li>
                    <li>
                      <span className="font-bold">Transfer type: </span>
                      {capitalizeFirstLetter(
                        this.props.selectedUserInfo.transfer_type
                      )}
                    </li>
                  </ul>
                </div>
              </div>
              <div>
                <h1>Uploaded Files</h1>
                <DownloadLink
                  linkFor="uploaded"
                  first_id={
                    this.props.selectedUserInfo.teacher_supporting_document
                  }
                  last_id={""}
                  title={"Supporting document"}
                />
              </div>
            </div>
            {/* Uploaded Files*/}
            {this.state.loading === false && (
              <div>
                <InputSelect
                  data={[
                    ...this.state.schools.map((el) => ({
                      title:
                        el.school_name + `( with ${el.vacant_posts} vacant(s)`,
                      value: el.school_code,
                    })),
                  ]}
                  error={
                    this.state.error && this.state.error.target === "school"
                      ? this.state.error.msg
                      : ""
                  }
                  name="select_level"
                  onChange={(e) =>
                    this.setState({ selectedSchool: e.target.value })
                  }
                  label="Change School:"
                  placeholder="Select"
                  value={this.state.selectedSchool}
                  size="md"
                  disabled={this.state.loading || this.state.requestLoading}
                />
                <TextArea
                  value={this.state.comment}
                  onChange={(e) => this.setState({ comment: e.target.value })}
                  name="comment"
                  error={
                    this.state.error && this.state.error.target === "comment"
                      ? this.state.error.msg
                      : ""
                  }
                  label={"Comment"}
                  placeholder={""}
                  disabled={this.state.loading || this.state.requestLoading}
                />
              </div>
            )}
            {this.state.loading === true && (
              <div className="mx-auto p-8">
                <Loading />
              </div>
            )}
          </div>
          {/*footer*/}
          {this.state.loading === false && (
            <div className="flex items-center justify-between p-3 border-t border-solid border-blueGray-200 rounded-b">
              <Button
                theme="default"
                onClick={() =>
                  this.setState({
                    error: null,
                    comment: "",
                    selectedSchool:
                      this.props.selectedUserInfo.requested_school_id,
                  })
                }
                disabled={this.state.loading || this.state.requestLoading}
              >
                Reset
              </Button>
              <div className="flex items-center justify-end gap-2">
                <Button
                  theme="success"
                  onClick={() => this.RequestHandle("APPROVED")}
                  loading={this.state.requestLoading}
                  disabled={this.state.loading || this.state.requestLoading}
                >
                  Approve
                </Button>
                <Button
                  theme="danger"
                  onClick={() => this.RequestHandle("REJECTED")}
                  loading={this.state.requestLoading}
                  disabled={this.state.loading || this.state.requestLoading}
                >
                  Reject
                </Button>
              </div>
            </div>
          )}
        </>
      </Modal>
    );
  }
}

export default React.memo(connect(null, { FC_RespondTransfer })(TransferForm));
