import React from "react";
import Container from "../../../components/ContainerComponents/Container";
import LeaveRequests from "./LeaveRequests";
import TransferRequests from "./TransferRequests";

enum TransferTabsType {
  transferTab = "Requested Transfers",
  leaveTab = "Requested Leaves",
}

const Index = () => {
  const [viewThisTab, setViewThisTab] = React.useState<TransferTabsType>(
    TransferTabsType.transferTab
  );
  return (
    <>
      <Container title="Requested Transfer" subTitle="Transfer">
        {/* Tab */}
        <div className="flex flex-wrap">
          <div className="w-full">
            <div className="flex flex-row items-center mt-2">
              {Object.values(TransferTabsType).map((item, i) => (
                <div
                  key={i}
                  onClick={() => setViewThisTab(item)}
                  className={`animate__animated ${
                    viewThisTab === item
                      ? "bg-accent-400 border-accent-900 text-accent-900 animate__zoomIn animate__faster"
                      : "bg-white border-white text-gray-600 cursor-pointer"
                  } hover:bg-accent-100 px-4 py-2 font-bold border-b-4 hover:border-accent-900 hover:text-accent-900 rounded-t mt-1 mx-1`}
                >
                  {item}
                </div>
              ))}
            </div>
            {/* Body */}
            <div className="w-full">
              {viewThisTab === TransferTabsType.transferTab && (
                <div>
                  <TransferRequests />
                </div>
              )}
              {viewThisTab === TransferTabsType.leaveTab && (
                <div>
                  <LeaveRequests />
                </div>
              )}
            </div>
          </div>
        </div>
      </Container>
    </>
  );
};

export const RequestApprovalDde = Index;
