import React from "react";
import { connect } from "react-redux";
import { FC_RespondLeave } from "../../../actions/transfer.action";
import Alert from "../../../components/Alert/Alert";
import Button from "../../../components/Buttons/Button";
import InputFile, { acceptAllEnum } from "../../../components/Inputs/InputFile";
import TextArea from "../../../components/Inputs/TextArea";
import { isEmptyOrSpaces } from "../../../utils/functions";

type LeaveFormProps = {
  selectedUserId: string;
  setSelectedUserId: (value: string) => void;

  // REDUX ========
  FC_RespondLeave: Function;
};

const LeaveForm: React.FC<LeaveFormProps> = (props) => {
  const [error, setError] = React.useState<{
    target: "main-error" | "main-success" | "approved_doc" | "comment";
    msg: string;
  } | null>(null);
  const [comment, setComment] = React.useState<string>("");
  const [selectedFile, setSelectedFile] = React.useState<File | null>(null);
  const [loadingRequest, setLoadingRequest] = React.useState<boolean>(false);

  const onFileChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    setError(null);
    // this.setState({ selectedFile: event.target.files[0] });

    // check if the file is available
    if (e.target.files === null) {
      setSelectedFile(null);
      return setError({
        msg: "Please select a valid file",
        target: "approved_doc",
      });
    }

    const thisFile = e.target.files[0];

    // check the file type
    if (thisFile.type !== "application/pdf") {
      setSelectedFile(null);
      return setError({
        msg: "The system accept pdf only",
        target: "approved_doc",
      });
    }

    // check if the size is valued
    if (thisFile.size <= 0) {
      setSelectedFile(null);
      return setError({
        msg: "Invalid file, Please choose a real file",
        target: "approved_doc",
      });
    }

    // Add image
    setSelectedFile(thisFile);
    setError(null);
  };

  const RequestHandle = (decision: "REJECTED" | "APPROVED") => {
    if (selectedFile === null) {
      setError({ target: "approved_doc", msg: "Required!" });
      return;
    }
    if (isEmptyOrSpaces(comment)) {
      setError({ target: "comment", msg: "Required!" });
      return;
    }
    props.FC_RespondLeave(
      props.selectedUserId,
      comment,
      selectedFile,
      decision,
      (status: boolean, response_error: string) => {
        setLoadingRequest(status);
        if (!status && !isEmptyOrSpaces(response_error))
          setError({ target: "main-error", msg: response_error });
        else if (isEmptyOrSpaces(response_error)) {
          setError({
            target: "main-success",
            msg: "Response sent successful!",
          });
          setComment("");
          setSelectedFile(null);
          setTimeout(() => {
            props.setSelectedUserId("");
          }, 1000);
        }
      }
      // (status: boolean, response_error: string) => {
      //   setLoadingRequest(status);
      //   if (validation_error) setError(validation_error);

      //   if (response_error) setError(response_error);

      //   if (response_error && response_error.target === "main-success") {
      //     setComment("");
      //     setSelectedFile(null);
      //     props.setSelectedUserId("");
      //   }
      // }
    );
  };

  return (
    <div className="animate__animated animate__zoomIn animate__faster absolute p-1 bg-primary-800 rounded -mt-0.5  z-40 w-auto mx-2 sm:mx-0 sm:w-80 right-0 ">
      {error && error.target === "main-success" && (
        <Alert theme="success" fixed>
          {error && error.msg}
        </Alert>
      )}
      {error && error.target === "main-error" && (
        <Alert theme="danger" fixed>
          {error && error.msg}
        </Alert>
      )}
      <div className="px-2">
        <InputFile
          disabled={loadingRequest}
          labelColor="text-gray-300"
          label="Signed document"
          name="approved_doc"
          className="p-2"
          onChange={(e) => onFileChange(e)}
          error={error && error.target === "approved_doc" ? error.msg : ""}
          type={"file"}
          accept={acceptAllEnum.PDF}
        />
      </div>
      <div className="px-2">
        <TextArea
          disabled={loadingRequest}
          name="dde_post_request_comment"
          className="w-full p-1 rounded"
          value={comment}
          onChange={(e) => setComment(e.target.value)}
          error={""}
          labelColor="text-gray-300"
          label={"Comment"}
          placeholder={""}
        ></TextArea>
      </div>
      <div className="flex justify-between gap-2">
        <Button
          theme="default"
          onClick={() => {
            props.setSelectedUserId("");
            setComment("");
            setSelectedFile(null);
          }}
          disabled={loadingRequest}
        >
          Cancel
        </Button>
        <div className="flex gap-2">
          <Button
            theme="success"
            disabled={loadingRequest}
            onClick={(e) => RequestHandle("APPROVED")}
          >
            Accept
          </Button>
          <Button
            theme="danger"
            disabled={loadingRequest}
            onClick={() => RequestHandle("REJECTED")}
          >
            Reject
          </Button>
        </div>
      </div>
    </div>
  );
};

export default connect(null, { FC_RespondLeave })(LeaveForm);
