import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth, Basic } from "../../../actions";
import {
  FC_GetAllLeaves,
  FT_GetAllLeaves,
} from "../../../actions/transfer.action";
import Alert from "../../../components/Alert/Alert";
import Button from "../../../components/Buttons/Button";
import DownloadLink from "../../../components/DownloadLink/DownloadLink";
import TextInput from "../../../components/Inputs/TextInput";
import TableLoading from "../../../components/TableLoading/TableLoading";
import { StoreState } from "../../../reducers";
import { TransferReducerType } from "../../../reducers/transfer.reducer";
import { TransferLeaveType } from "../../../types/TransferTypes";
import { DATE, isEmptyOrSpaces } from "../../../utils/functions";
import LeaveForm from "./LeaveForm";

type LeaveRequestsProps = {
  auth: Auth;
  basic: Basic;
  transfer: TransferReducerType;
  FC_GetAllLeaves: FT_GetAllLeaves;
};

type LeaveRequestsState = {
  loading: boolean;
  error: {
    target: "main-error" | "main-success";
    msg: string;
  } | null;
  search: string;
  selectedUserId: string;
};

class LeaveRequests extends Component<LeaveRequestsProps, LeaveRequestsState> {
  constructor(props: LeaveRequestsProps) {
    super(props);

    this.state = {
      loading: true,
      error: null,
      search: "",
      selectedUserId: "",
    };
  }

  componentDidMount = () => {
    if (
      this.props.transfer.allLeaves === null ||
      this.props.transfer.allLeaves.length === 0
    )
      this.props.FC_GetAllLeaves(
        (status: boolean, data: TransferLeaveType[] | null, msg: string) => {
          this.setState({ loading: status });

          if (!status && data) this.setState({ loading: status });
          else if (!status && !isEmptyOrSpaces(msg))
            this.setState({
              error: { target: "main-error", msg: msg },
            });
        }
      );
    else if (this.state.loading === true) this.setState({ loading: false });
  };

  render() {
    return (
      <>
        {this.state.error && this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}
        {this.state.error && this.state.error.target === "main-error" && (
          <Alert theme="danger" fixed>
            {this.state.error.msg}
          </Alert>
        )}
        <div className="mt-3">
          <div className="border border-t mt-2 mb-1 flex items-center px-3 rounded">
            <div className="flex-1 pr-2">
              <TextInput
                error=""
                label="Search"
                name=""
                type="search"
                onChange={(e) => this.setState({ search: e.target.value })}
                placeholder="Search by district name"
                value={this.state.search}
                size="sm"
                disabled={
                  this.state.loading ||
                  this.props.transfer.allLeaves === null ||
                  this.props.transfer.allLeaves.length === 0
                }
              />
            </div>
          </div>
          <div className="w-full overflow-x-auto">
            <table className="w-full text-left border">
              <thead>
                <tr>
                  <th className="p-2">#</th>
                  <th className="p-2">Names</th>
                  <th className="p-2">Phone</th>
                  <th className="p-2">Qualification</th>
                  <th className="p-2">Position</th>
                  <th className="p-2">Requested School</th>
                  <th className="p-2">Requested Sector</th>
                  <th className="p-2">Requested Date</th>
                  <th className="p-2">Status</th>
                </tr>
              </thead>
              <tbody>
                {this.state.loading ? (
                  <TableLoading cols={9} />
                ) : this.props.transfer.allLeaves === null ? (
                  <tr className="border-t border-b">
                    <td
                      colSpan={9}
                      className="p-6 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                    >
                      Something went wrong! Please try again!
                    </td>
                  </tr>
                ) : this.props.transfer.allLeaves.length <= 0 ? (
                  <tr className="border-t border-b">
                    <td
                      colSpan={9}
                      className="p-6 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                    >
                      No results found
                    </td>
                  </tr>
                ) : (
                  this.props.transfer.allLeaves.map((transfer, index) => {
                    return (
                      <tr
                        key={transfer.teacher_transfer_id}
                        className="border-t border-b hover:bg-accent-400"
                      >
                        <td className="px-2">{index + 1}</td>
                        <td className="px-2">{transfer.teacher_full_name}</td>
                        <td className="px-2">
                          {transfer.teacher_phone_number}
                        </td>
                        <td className="px-2">{transfer.qualification_name}</td>
                        <td className="px-2">{transfer.position_name}</td>
                        <td className="px-2">
                          {transfer.requested_school_info.school_name}
                        </td>
                        <td className="px-2">
                          {transfer.requested_school_info.sector_name}
                        </td>
                        <td className="p-2">
                          {DATE(transfer.teacher_requested_transfer_date)}
                        </td>
                        {this.state.selectedUserId ===
                        transfer.teacher_transfer_id.toString() ? (
                          <td className="">
                            <LeaveForm
                              selectedUserId={this.state.selectedUserId}
                              setSelectedUserId={(value: string) =>
                                this.setState({ selectedUserId: value })
                              }
                            />
                          </td>
                        ) : (
                          <>
                            <td className="p-2">
                              {transfer.outgoing_status === "PENDING" ? (
                                <Button
                                  theme="accent-light"
                                  onClick={(e) =>
                                    transfer.outgoing_status === "PENDING" &&
                                    this.setState({
                                      selectedUserId:
                                        transfer.teacher_transfer_id.toString(),
                                    })
                                  }
                                >
                                  Respond
                                </Button>
                              ) : (
                                transfer.outgoing_status
                              )}
                            </td>
                            <td className="p-2">
                              {transfer.outgoing_status === "PENDING" ? (
                                <DownloadLink
                                  linkFor="transfer"
                                  first_id={transfer.teacher_transfer_id}
                                  last_id={transfer.teacher_id}
                                  title={"Transfer letter"}
                                />
                              ) : (
                                transfer.mayor_letter && (
                                  <DownloadLink
                                    linkFor="uploaded"
                                    first_id={transfer.mayor_letter}
                                    last_id={""}
                                    title={"Transfer letter"}
                                  />
                                )
                              )}
                            </td>
                          </>
                        )}
                      </tr>
                    );
                  })
                )}
              </tbody>
            </table>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  transfer,
}: StoreState): { auth: Auth; basic: Basic; transfer: TransferReducerType } => {
  return { auth: auth, basic: basic, transfer };
};

export default connect(mapStateToProps, {
  FC_GetAllLeaves,
})(LeaveRequests);
